<template>
    <form action="https://payment-stage.ecpay.com.tw/Cashier/AioCheckOut/V5" method="post">
        <input type="hidden" name="CheckMacValue" :value="checkMacValue" />
        <input v-for="(name, index) in formPropertiesName" :key="index" type="hidden" :name="name" :value="form[name]" />
        <slot name="submitbutton"></slot>
    </form>
</template>

<script>
import ecPayTool from "../ecPayTool";
export default {
    props: {
        TotalAmount: {
            type: String,
        },
        MerchantTradeDate: {
            type: String,
        },
        ItemName: {
            type: String,
        },
        Email: {
            type: String,
        },
    },
    data() {
        let myForm = {
            ChoosePayment: "ALL",
            ClientBackURL: "http://localhost:8080/#/About",
            EncryptType: 1,
            ItemName: "貓咪T袖",
            MerchantID: "2000132",
            MerchantTradeDate: "",
            MerchantTradeNo: "",
            PaymentType: "aio",
            ReturnURL: "http://www.goldappio.com/NodeJsAPIAdapter/ShopApi/validateOrder/3000",
            // StoreID: "kfcShop",
            TotalAmount: 1000,
            TradeDesc: "貓咪的T袖",
        };

        let allPropertyNames = Object.getOwnPropertyNames(myForm).sort();
        // allPropertyNames.splice(allPropertyNames.length - 1, 1);

        return {
            HashKey: "5294y06JbISpM5x9",
            HashIV: "v77hoKGq4kWxNNIS",
            formPropertiesName: allPropertyNames,
            form: myForm,
            checkMacValue: "",
        };
    },

    async mounted() {
        this.form.ItemName = this.ItemName;
        this.form.TradeDesc = this.ItemName;
        this.form.TotalAmount = parseInt(this.TotalAmount);
        this.form.MerchantTradeDate = this.MerchantTradeDate;

        this.form.MerchantTradeNo = ecPayTool.generateMerchantTradeNo();
        this.checkMacValue = ecPayTool.buildCheckMacValue(this.form, this.HashKey, this.HashIV, this.formPropertiesName);
    },
};
</script>

