import Hashes from "jshashes";
import { crc32 } from "easy-crc";
import { v4 as uuidv4 } from "uuid";

//不重複訂單編號產生
export default {
    generateMerchantTradeNo() {
        return crc32("CRC-32", uuidv4()).toString(16);
    },
    buildCheckMacValue(form, HashKey, HashIV, allPropertyNames) {
        let szCheckMacValue = "";

        let concatProperties = "";
        allPropertyNames.forEach((p) => {
            concatProperties += "&" + p + "=" + form[p];
        });

        szCheckMacValue = "HashKey=" + HashKey + concatProperties + "&HashIV=" + HashIV;
        szCheckMacValue = this.encodeNetUrl(szCheckMacValue);

        szCheckMacValue = new Hashes.SHA256().hex(szCheckMacValue);
        return szCheckMacValue.toUpperCase();
        //產生檢查碼
    },
    //綠界專用encode
    encodeNetUrl(str) {
        str = encodeURIComponent(str).toLowerCase();
        str = str.replaceAll("%2d", "-");
        str = str.replaceAll("%5f", "_");
        str = str.replaceAll("%2e", ".");
        str = str.replaceAll("%21", "!");
        str = str.replaceAll("%2a", "*");
        str = str.replaceAll("%28", "(");
        str = str.replaceAll("%29", ")");
        str = str.replaceAll("%20", "+");
        return str;
    },
};
